.header {
	background-color: $black-back;
	position: relative;
	flex: 0 0 auto;

	&__wrp {
		@include flex-space();
		min-height: 75px; }

	&__wrp-controls {
		@include flex-center(); }

	&__logo-link {
		margin-right: 41px; }

	&__search-form {
		display: inline-block; }

	&__link {
		margin-left: 31px;
		@include mobile {
			display: none; } }

	&__icon {
		fill: $white-icon; }

	&__menu-btn {
		border: none;
		background-color: transparent; }

	&__nav {
		z-index: 100;
		display: none;
		box-sizing: border-box;
		@include offset(75,0);
		background-color: white;
		@include size(232,764);
		padding-left: 34px;
		box-shadow: 6px 4px 35px rgba(0, 0, 0, 0.21); }

	&__menu-btn:focus + &__nav,
	&__nav:hover {
		display: block; }

	&__menu-title {
		@include font(14,17,bold);
		color: black;
		text-transform: uppercase;
		margin-bottom: 24px;
		margin-top: 37px; } }

