.form {
	&__input {
		border: 1px solid #A4A4A4;
		background-color: white;
		width: 100%;
		height: 44px;
		margin-bottom: 20px;
		padding: 12px 17px;
		box-sizing: border-box;
		transition-property: opacity;
		transition-duration: .3s;
		opacity: 1;
		&:focus {
			border: solid 1px $pink-border-button;
			outline: none; }
		&:focus {
			&::placeholder {
				color: $pink-text; } }

		&:hover {
			opacity: 0.6;
			transition-property: opacity;
			transition-duration: .3s; } }

	&__radio-label {
		vertical-align: middle;
		line-height: 20px;
		@include size(auto,20);
		display: flex;
		align-items: center;
		margin-bottom: 20px;
		margin-right: 20px;
		cursor: pointer;

		&::before {
			content: '';
			display: inline-block;
			@include size(15,15);
			border: solid 2px white;
			box-shadow: 0 0 0 1px #A4A4A4;
			margin-right: 10px;
			border-radius: 50% 50%; }

		&:hover {
			color: $pink-text; }

		&:hover {
			&::before {
				color: $pink-text;
				box-shadow: 0 0 0 1px $pink-border-button; } } }

	&__radio {
		&:checked + {
			.form__radio-label {
				&:before {
					background: $black-back;
					background-size: 10px 10px; } } } }

	&__email,
	&__submit {
		border: none;
		margin: 0;
		height: 50px;
		display: block; }

	&__email {
		width: 256px;
		padding: 7px 22px;
		box-sizing: border-box;
		border-top-left-radius: 25px;
		border-bottom-left-radius: 25px;
		outline: none;
		background-color: #E1E1E1;
		@include font(14,17);
		color: $black-text;
		opacity: 0.67;
		&:focus {
			outline: none;
			opacity: 0.9; } }

	&__submit {
		width: 100px;
		background-color: #E05C6E;
		border-top-right-radius: 25px;
		border-bottom-right-radius: 25px;
		@include font(14,17);
		text-align: center;
		color: white;
		cursor: pointer;
		&:hover {
			opacity: 0.6; }
		&:active {
			opacity: 0.8; } } }

input[type=range] {
	height: 47px;
	-webkit-appearance: none;
	margin: 10px 0;
	width: 150px; }

input[type=range]:focus {
	outline: none; }

input[type=range]::-webkit-slider-runnable-track {
	width: 150px;
	height: 1px;
	cursor: pointer;
	animate: 0.2s;
	box-shadow: 0px 0px 0px #000000;
	background: #B6B6B6;
	border-radius: 0px;
	border: 0px solid #8A8A8A; }

input[type=range]::-webkit-slider-thumb {
	box-shadow: 0px 0px 0px #828282;
	border: 1px solid #EF5B70;
	height: 40px;
	width: 4px;
	border-radius: 0px;
	background: #EF5B70;
	cursor: pointer;
	-webkit-appearance: none;
	margin-top: -20px; }

input[type=range]:focus::-webkit-slider-runnable-track {
	background: #B6B6B6; }

input[type=range]::-moz-range-track {
	width: 150px;
	height: 1px;
	cursor: pointer;
	animate: 0.2s;
	box-shadow: 0px 0px 0px #000000;
	background: #B6B6B6;
	border-radius: 0px;
	border: 0px solid #8A8A8A; }

input[type=range]::-moz-range-thumb {
	box-shadow: 0px 0px 0px #828282;
	border: 1px solid #EF5B70;
	height: 40px;
	width: 4px;
	border-radius: 0px;
	background: #EF5B70;
	cursor: pointer; }

input[type=range]::-ms-track {
	width: 150px;
	height: 1px;
	cursor: pointer;
	animate: 0.2s;
	background: transparent;
	border-color: transparent;
	color: transparent; }

input[type=range]::-ms-fill-lower {
	background: #B6B6B6;
	border: 0px solid #8A8A8A;
	border-radius: 0px;
	box-shadow: 0px 0px 0px #000000; }

input[type=range]::-ms-fill-upper {
	background: #B6B6B6;
	border: 0px solid #8A8A8A;
	border-radius: 0px;
	box-shadow: 0px 0px 0px #000000; }

input[type=range]::-ms-thumb {
	margin-top: 1px;
	box-shadow: 0px 0px 0px #828282;
	border: 1px solid #EF5B70;
	height: 40px;
	width: 4px;
	border-radius: 0px;
	background: #EF5B70;
	cursor: pointer; }

input[type=range]:focus::-ms-fill-lower {
	background: #B6B6B6; }

input[type=range]:focus::-ms-fill-upper {
	background: #B6B6B6; }
