@include add-fonts(Lato);

.page {
	font-family: $font-lato;
	@include font(14,17,300);
	display: flex;
	flex-direction: column;
	height: 100%; }

.container {
	width: 1140px;
	margin: 0 auto;

	@include tablet {
		width: 100%;
		box-sizing: border-box;
		padding: 0 32px; }

	@include mobile {
		padding: 0 16px; } }

.visually-hidden {
	position: absolute;
	@include size(1,1);
	margin: -1px;
	padding: 0;
	overflow: hidden;
	border: 0;
	clip: rect(0 0 0 0); }

button {
	cursor: pointer; }

.close-btn {
	float: right;
	margin: 16px;
	position: relative;
	@include size(12,12);
	cursor: pointer;
	&:before,
	&:after {
		content: '';
		position: absolute;
		@include size(17,2);
		background-color: #6F6E6E;
		@include offset(5,false,false,-2); }

	&:before {
		transform: rotate(-45deg); }

	&:after {
		transform: rotate(45deg); }

	@include mobile {
		margin: 0;
		@include size(9,9);
		&:before,
		&:after {
			@include size(10,2); } } }

html {
	height: 100%; }
main {
	flex: 1 0 auto; }
