.sales {
	margin-top: 65px;
	@include tablet {
		margin-top: 20px; }
	@include mobile {
		margin-top: 65px; }

	&__wrp {
		@include wrapper(40%);
		@include mobile {
			@include size(full,auto);
			padding-top: 0; } }

	&__list {
		@include flex-space();
		flex-wrap: wrap;
		@include offset(0,0,0,0);

		@include mobile {
			position: static; } }

	&__item {
		flex: 1;
		height: 60%;
		box-sizing: border-box;
		position: relative;

		&:nth-child(2) {
			margin: 0 30px 30px;

			@include tablet {
				margin: 0 20px 20px; }

			@include mobile {
				margin: 0 0 32px; } }

		&:last-child {
			margin: 0; }

		@include mobile {
			flex: none;
			@include wrapper(72%);
			margin-bottom: 32px;
			@include size(full); }

		&_woman {
			background-image: url("../img/sales-woman.jpg"); }
		&_man {
			background-image: url("../img/sales-man.jpg"); }
		&_kids {
			background-image: url("../img/sales-kids.jpg"); }
		&_accesories {
			background-image: url("../img/sales-accesories.jpg"); }
		&_lower {
			flex: none;
			width: 100%;
			height: 40%;
			@include mobile {
				@include wrapper(32%); } } }

	&__link {
		@include size(full,full);
		margin: 0px;
		@include flex-center(column);
		@include background();
		text-decoration: none;

		&::before,
		&::after {
			content: '';
			@include size(0,0);
			box-sizing: border-box;
			display: block;
			border: none;
			transition-duration: 1s;
			opacity: 0; }

		&::before {
			@include offset(0,false,false,0);
			border-top: solid 5px $pink-border-button;
			border-left: solid 5px $pink-border-button; }

		&::after {
			@include offset(false,0,0,false);
			border-right: solid 5px $pink-border-button;
			border-bottom: solid 5px $pink-border-button; }

		&:hover {
			&::before,
			&::after {
				opacity: 1; } }

		&:hover {
			&::before,
			&::after {
				@include size(full,full);
				transition-duration: 1s; } }

		&:active {
			&::before,
			&::after {
				background-color: white;
				opacity: 0; } }

		@include mobile {
			@include offset(0,0,0,0); } }

	&__text {
		@include font(16,19);
		text-transform: uppercase;
		color: white;
		@include tablet; }


	&__item-title {
		@include font(24,29,700);
		color: $pink-text;
		text-transform: uppercase; } }
